body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#0a192f;
  color:white!important;

}
.maintext{
  animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}
@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.socialbutton{
  background-color:transparent!important;
      font-size: 45px;
}
.socialbutton:hover{
      color:#7aff7a!important;
      -webkit-transform: scale(1.3);
}
.footer{
  background-color: transparent!important;
}
.primarycolor{
  color:#7aff7a!important;
  text-shadow: 1px 1px 1px #7aff7a, 1px 1px 2px#7aff7a;
}
.aboutcolumns{
  padding: 10%;
}
.bold{
  font-weight: bold;
}
.navbar{
  background-color: #0a192f!important;
}
.tag{
      border-radius: 50px 0px 0px 50px!important;
      margin: 3px;
      font-weight: 800;
}
.tag:hover{
      -webkit-transform: scale(1.1);
      background-color: #7aff7a!important;
}
.navbar-item:hover{
      -webkit-transform: scale(1.2);
      background-color:#fff0!important;
}
.navbar-item{
      font-weight: bold;
          font-size: 19px
}
.tagplacement{
      padding-top: 100px;
}
.tagHeader{
      padding-bottom: 15px;
      font-size: 25px;
}
.cyxx{
  font-weight: 400;
  font-size: 20px;
}
.cyxv{
  font-size: 30px;
}
.tags{
  display: block;
}
.navbar-brand{
      height: 100px;
}
.burger{
      height: 100%;
}
.abouttext{
      font-size: 25px;
}

.port{
  display:none;
}
.servicescolumn{
  background-color: transparent;
    border: 3px solid #7aff7a!important;
    color: white!important;
    font-size: 25px;
    margin:10px;
    box-shadow: 0 0 10px #7aff7a;
}
.content{
  color:white!important;
}
.box{
    background-color: transparent;
}
.section-heading{
  padding-bottom: 20px;
}
#portfolio{
  display:none;
}
.emailbtn,.portbtn{
  font-size: 25px;
      background-color: transparent;
      border: 1px solid #7aff7a!important;
      color: #7aff7a!important;
          border-radius: 50px;
          box-shadow: 0 0 3px #7aff7a;
          font-weight: bold;
}
.emailbtn:hover{
  background-color:#6ae14d29!important;
  -webkit-transform: scale(1.02);
}
.portbtn:hover{
  background-color:#6ae14d29!important;
  -webkit-transform: scale(1.02);
}
.uparrowframe{
  position: fixed;
    bottom: 0;
    width:100%;
}
.uparrow{
  font-size: 100px;
}
.mypic{
  border-radius: 50%;
          margin-bottom: 60px;
}
.servicesdesc{
  font-size: 1.15rem;
}
#resume{
      padding-top: 150px;
}
.card{
  background-color: transparent!important;
  border: 5px solid #7aff7a!important;
  border-radius: 25px!important;
}
h1, h2, h3, h4, a, p, b, strong, td{
  color: white!important;
}
.navbar-end{
      padding: 20px;
}

@media (max-width: 1024px) {
  .navbar-end{
        padding: 0px;
  }
  .navbar-menu{
    background-color: #12264463!important;
  }
}
